@import 'sass-rem';
@import '~styles/mixins';
@import '~styles/variables';
@import '~styles/project';

.task {
  @include position(relative);
  @include content-vertical-center;

  &__text {
    align-self: flex-start;
  }

  &__img {
    @include size(240px, auto);

    margin-left: 80px;
  }

  .brief {
    @include size(fit-content, 40px);
    @include content-center;

    &__title {
      @include field-content-strong-font;
    }

    &__remark {
      @include field-content-big-font;

      margin-left: 20px;
    }
  }

  .period {
    @include size(fit-content, 32px);
    @include content-center;

    &__icon {
      @include size(16px, 16px);

      g {
        fill: $field-clock-icon-color;
      }
    }

    &__text {
      @include field-content-big-font;

      margin-left: 8px;
    }
  }

  &__description {
    @include field-content-font;

    margin-top: 8px;
  }
}

.task--title-row-arrangement {
  .task__title {
    @include content-vertical-center;

    .period {
      margin-left: 30px;
    }
  }
}

.task--with-line {
  padding-left: 25px;

  &::before {
    @include size(10px, 10px);
    @include position(absolute, $top: -5px, $left: -5px);
    @include draw($task-line-color);

    content: '';
    border-radius: 100%;
  }

  &::after {
    @include size(1px, 96%);
    @include position(absolute, $top: 5px, $left: -1px);
    @include draw($task-line-color);

    content: '';
  }
}
