@import 'sass-rem';
@import '~styles/mixins';
@import '~styles/variables';
@import '~styles/project';

.skill-card {
  .level-names {
    @include size(fit-content, 100%);
    @include content-vertical-center;

    margin-left: 240px;

    &__level-name {
      @include size(64px, 24px);
      @include content-center;
      @include field-content-big-font;

      &:not(:nth-of-type(1)) {
        margin-left: 24px;
      }
    }
  }

  .skill-category {
    @include field-content-big-font;
  }

  .skill-bars {
    margin-top: 15px;

    &__skill-bar:not(:nth-of-type(1)) {
      margin-top: 15px;
    }
  }
}

.skill-card--indent {
  .level-names,
  .skill-bars {
    padding-left: 40px;
  }
}
