// nav-font
$nav-font: 'Noto Sans TC', sans-serif;
$nav-font-weight: 400;
$nav-font-size: 16px;
$nav-font-color: #fff;
$nav-letter-spacing: 0.14px;
$nav-line-height: 32px;

// name-font
$name-font: 'Noto Sans TC', sans-serif;
$name-font-weight: 400;
$name-font-size: 34px;
$name-font-color: #fff;
$name-letter-spacing: 0;
$name-line-height: 64px;
$name-text-shadow: 0 0 15px rgba(83, 83, 83, 0.35);

