@import 'sass-rem';
@import '~styles/mixins';
@import '~styles/variables';
@import '~styles/project';

.footer {
  @include content-center;

  margin-bottom: 20px;

  .works {
    @include size(400px, 40px);
    @include content-center;
    @include draw(#ffffff);
    @include field-content-big-font;

    text-decoration: none;
    border-radius: 20px;
    box-shadow: 0 12px 22px 0 rgba(82, 87, 104, 0.1),
      0 -12px 22px 0 rgba(82, 87, 104, 0.1);
    transition: 0.5s;

    &:hover {
      @include draw(rgba(204, 204, 204, 0.2));
    }
  }

  .behance {
    color: #81b7ff;
  }

  .go-to-top {
    @include position(fixed, $bottom: 25px, $right: 25px, $z-index: 1);

    cursor: pointer;
    transition: 0.75s;
  }
}
