@import 'sass-rem';
@import '~styles/mixins';
@import '~styles/variables';
@import '~styles/project';

.skill-bar {
  @include size(660px, 40px);
  @include content-vertical-center;

  .skill-name {
    @include size(210px, 100%);
    @include content-vertical-center;

    &__icon {
      @include size(32px, 32px);
    }

    &__text {
      @include field-content-big-font;
    }
  }

  .level-bar {
    @include size(fit-content, 100%);
    @include content-vertical-center;

    margin-left: 30px;

    &__level {
      @include size(64px, 24px);

      border-radius: 4px;

      &:not(:nth-of-type(1)) {
        margin-left: 24px;
      }
    }

    &__level--gray {
      @include draw($default-level-background-color);
    }

    &__level--colorful {
      &:nth-of-type(1) {
        @include draw($first-level-background-color);
      }

      &:nth-of-type(2) {
        @include draw($second-level-background-color);
      }

      &:nth-of-type(3) {
        @include draw($third-level-background-color);
      }

      &:nth-of-type(4) {
        @include draw($forth-level-background-color);
      }

      &:nth-of-type(5) {
        @include draw($fifth-level-background-color);
      }
    }
  }
}

.skill-bar--with-icon {
  .skill-name {
    &__text {
      margin-left: 24px;
    }
  }
}
