@import 'sass-rem';
@import '~styles/mixins';
@import '~styles/variables';

@mixin nav-font {
  @include text(
    $nav-font,
    $nav-font-size,
    $nav-font-weight,
    $nav-font-color,
    $nav-letter-spacing,
    $nav-line-height
  );

  @content;
}

