@mixin field-title {
  .title {
    @include size(fit-content, 50px);
    @include content-center;

    &__icon {
      @include size(28px, 28px);

      g,
      path {
        fill: $field-title-icon-color;
      }
    }

    &__text {
      @include field-title-font;

      margin-left: 12px;
    }
  }
}

@mixin hover-dialog {
  @include position(relative);

  &::before,
  &::after {
    opacity: 0;
    transition: 0.75s;
  }

  &::before {
    @include position(absolute, $top: -50px);
    @include contact-font;
    @include draw(#373737);

    padding: 5px 10px;
    white-space: nowrap;
    content: attr(data-before);
    border-radius: 6px;
    box-shadow: 4px 4px 12px 0 rgba(25, 42, 70, 0.18);
  }

  &::after {
    @include size(0, 0);
    @include horizontal-center($top: -8px);

    content: '';
    border-color: #373737 transparent transparent;
    border-style: solid;
    border-width: 5px 5px 0;
  }

  &:hover {
    &::before,
    &::after {
      opacity: 0.8;
    }
  }
}
