@import 'sass-rem';
@import '~styles/mixins';
@import '~styles/variables';
@import '~styles/project';

.header {
  @include size(100%, 632px);
  @include position(relative);

  background-image: linear-gradient(
    45deg,
    rgba(129, 183, 255, 0.75) 0%,
    rgba(237, 178, 255, 0.75) 100%
  );

  &::before {
    @include size(100%, 100%);
    @include position(absolute, $z-index: -1);

    content: '';
    background-image: url('../../assets/profileBackground.png');
    opacity: 0.8;
  }

  :global {
    .headroom-wrapper {
      height: 0 !important;
    }

    .headroom {
      @include position(fixed, $top: 25px, $right: 25px, $z-index: 1);

      transition: 0.5s;
    }

    .headroom--unpinned {
      transform: translateY(-150%);
    }
  }

  .nav {
    @include size(fit-content, 50px);
    @include content-center;
    @include nav-font;
    @include draw(rgba(0, 0, 0, 0.4));

    border-radius: 20px;

    &__link {
      @include size(110px, 100%);
      @include position(relative);
      @include content-center;

      cursor: pointer;
      transition: 0.75s;

      &:hover {
        @include draw(rgba(0, 0, 0, 0.45));
      }

      &:first-of-type {
        border-top-left-radius: 20px;
        border-bottom-left-radius: 20px;
      }

      &:last-of-type {
        border-top-right-radius: 20px;
        border-bottom-right-radius: 20px;
      }

      &:not(:last-of-type) {
        &::after {
          @include size(1px, 70%);
          @include position(absolute, $right: -1px);
          @include draw(rgba(255, 255, 255, 0.1));

          content: '';
        }
      }
    }
  }

  .profile {
    @include content-center;
    @include contact-font;

    flex-direction: column;

    &__picture {
      margin-top: 145px;
    }

    &__name {
      @include name-font;

      margin-top: 20px;
    }

    &__icon {
      margin-right: 10px;
    }

    &__contact {
      @include content-center;
    }

    &__works {
      @include size(120px, 32px);
      @include content-center;
      @include draw(transparent, 1px solid #ffffff);
      @include contact-font;

      margin-top: 8px;
      text-decoration: none;
      border-radius: 20px;
      transition: 0.7s;

      &:hover {
        @include draw(rgba(255, 255, 255, 0.2));
      }
    }
  }
}
