@import 'sass-rem';
@import '~styles/mixins';
@import '~styles/variables';

.skill-classification {
  @include content-center;

  .decoration-icon {
    @include size(210px, 210px);
    @include position(relative, $left: 40px, $z-index: -1);

    min-width: 210px;
    min-height: 210px;
  }

  .skill-cards {
    @include draw(#fff);

    padding: 40px 128px 40px 40px;
    border-radius: 4px;
    box-shadow: 4px 4px 12px 0 rgba(25, 42, 70, 0.18);
  }
}
