@import 'sass-rem';
@import '~styles/mixins';
@import '~styles/variables';
@import '~styles/project';

.project-card {
  @include size(1110px);
  @include draw(#fff);

  padding: 35px 65px;
  border-radius: 4px;
  box-shadow: 4px 4px 12px 0 rgba(25, 42, 70, 0.18);

  .header {
    @include position(relative);
    @include content-vertical-center;

    &__project-name {
      @include project-title-font;
    }

    &__link {
      @include hover-dialog;
      @include size(35px, 35px);
      @include content-center;

      margin-left: 15px;
    }

    &__link-icon {
      @include size(24px, 24px);
    }

    &__used-tools {
      @include hover-dialog;
      @include size(fit-content, 45px);
      @include vertical-center($right: 0);
      @include content-center;
      @include draw(rgba(238, 238, 238, 0.7));

      padding: 0 25px;
      border-radius: 20px;

      .tool {
        @include size(32px, 32px);

        &:not(:last-of-type) {
          margin-right: 16px;
        }
      }
    }
  }

  .content {
    margin-top: 16px;

    &__subtitle {
      @include field-content-strong-font;
    }

    &__description {
      @include field-content-font;

      margin-top: 10px;
    }
  }

  .screenshots {
    margin-top: 16px;

    &__screenshot {
      @include size(980px, auto);
    }
  }

  iframe {
    margin-top: 16px;
  }
}
